import React from "react";
import "./App.css";
import { HashRouter as Router, Switch, Route, Link } from "react-router-dom";
import SecretCodeInput from "./slides/SecretCodeInput";
import CardExplanation from "./slides/CardExplanation";
import PictureTimeline from "./slides/PictureTimeline";

function App() {
  window.playingAudio = new Audio("/assets/gambler.mp3");

  window.addEventListener(
    "hashchange",
    function (event) {
      if (this.document.location.hash !== "#/") {
        if (window.playingAudio.paused) {
          window.playingAudio.play();
        }
      } else {
        window.playingAudio.pause();
      }
    },
    false
  );

  return (
    <div>
      <Router>
        <Switch>
          <Route exact path="/">
            <SecretCodeInput />
          </Route>
          <Route exact path="/birthday-card-explanation">
            <CardExplanation />
          </Route>
          <Route exact path="/picture-timeline">
            <PictureTimeline />
          </Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
