import React, { Component } from "react";
import { Col, Row, FormControl, Button } from "react-bootstrap";

export default class SecretCodeInput extends Component {
  handleSecretCodeGuess() {
    // @ts-ignore
    let guess = document.getElementById("secret-code-input").value;

    if (guess == "nz-bday-surprise") {
      window.location.hash = "#/birthday-card-explanation";
    } else {
      // TODO: Handle errors in a fun way.
    }
  }

  render() {
    return (
      <div
        id="secret-code-input-slide"
        className="full-view-slide-container no-scroll fade-in-fast"
      >
        <Row className="h-100">
          <Col className="h-100 center-content">
            <div className="d-flex flex-column justify-content-center">
              <h4>Please enter the secret code.</h4>
              <div className="text-right py-2">
                <FormControl
                  id="secret-code-input"
                  placeholder="Secret Code"
                ></FormControl>
                <Button
                  type="submit"
                  variant="outline-primary"
                  className="mt-2"
                  onClick={this.handleSecretCodeGuess}
                >
                  Guess!
                </Button>
              </div>
            </div>
          </Col>
        </Row>
      </div>
    );
  }
}
